* {
    box-sizing: border-box;
}

.header-outer-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    max-height: 800px;
    text-align: center;
}

.header-container {
    height: 86vh;
    max-height: 800px;
    width: 96vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background: linear-gradient(270deg, #babee2, #9bcde4, #c3a3ea, #f5d59e);
    background-size: 800% 800%;
    border-radius: 10px;
    -webkit-animation: gradientAnimation 45s ease infinite;
    -moz-animation: gradientAnimation 45s ease infinite;
    animation: gradientAnimation 45s ease infinite;
}

@-webkit-keyframes gradientAnimation {
    0% {
        background-position: 0% 50%
    }

    50% {
        background-position: 100% 50%
    }

    100% {
        background-position: 0% 50%
    }
}

@-moz-keyframes gradientAnimation {
    0% {
        background-position: 0% 50%
    }

    50% {
        background-position: 100% 50%
    }

    100% {
        background-position: 0% 50%
    }
}

@keyframes gradientAnimation {
    0% {
        background-position: 0% 50%
    }

    50% {
        background-position: 100% 50%
    }

    100% {
        background-position: 0% 50%
    }
}

.container {
    margin: 0 auto;
}

h1 {
    font-weight: 300;
    font-size: 12rem;
    line-height: 0.8;
}

h2 {
    font-weight: 200;
    font-size: 5rem;
    letter-spacing: 0.1px;
}

.header-bottom-texts {
    margin-bottom: 2vh;
    width: 95%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.tempdiv {
    height: 12vh;
}

@media screen and (max-width: 450px) {
    .header-outer-container {
        height: 70vh;
    }

    .container {
        width: 95vw;
    }

    .header-container {
        height: 60vh;
    }

    h1 {
        font-size: 5rem;
    }

    h2 {
        font-size: 2rem;
    }

    .header-bottom-texts {
        font-size: 0.8rem;
    }
}